import React, { Component } from 'react'
import styled from 'styled-components'
import gql from 'graphql-tag'
import { Query, Mutation } from 'react-apollo'
import DaySelector from './DaySelector'
import { format } from 'date-fns'
import Timer from './TimePicker'
import User from './User'

const SEND_TEXT_MUTATION = gql`
  mutation SEND_TEXT_MUTATION(
    $to: String!
    $from: String!
    $text: String!
    $client: ID!
    $time: String!
    $date: DateTime
  ) {
    createTextReminder(to: $to, from: $from, text: $text, client: $client, time: $time, $date: DateTime) {
      id
      text
      client {
        id
      }
      confirmationStatus
    }
  }
`

const TEXT_TEMPLATES_QUERY = gql`
  query TEXT_TEMPLATES_QUERY {
    textTemplates {
      id
      name
      content
    }
  }
`

const Back = styled.div`
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-rows: 70px 1fr;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  padding: 20px 20px;
  z-index: 5;
  border-radius: 20px;
  border: 5px solid white;
  box-shadow: 1px 0 4px 2px rgba(0, 0, 0, 0.1);
  background-color: rgba(240, 240, 240, 1);
`

