const MessageEdit = styled.textarea`
  grid-column: 2;
  grid-row: 2;
  position: relative;
  z-index: 6;
  width: 100%;
  padding: 10px;
  height: 100%;
  border-radius: 10px;
  font-size: 2rem;
  /* justify-self: center; */
  resize: none;
  border: none;
  box-shadow: 0 0 4px 3px rgba(0, 0, 0, 0.05);
  &:focus {
    outline: none;
  }
`

const Send = styled.button`
  background: rgba(30, 110, 240, 1);
  color: white;
  font-weight: 800;
  border: none;
  width: 75px;
  font-size: 2rem;

  height: 75px;
  font-size: 1.8rem;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.05);
  &:active {
    box-shadow: none;
  }
  &[disabled] {
    opacity: 0.5;
  }
  &:hover {
    background: rgba(30, 110, 240, 0.9);
  }
`

class ReviewMessage extends Component {
  state = {
    time: '8:00 am',
    date: '',
    text: '',
  }

  handleDate = e => {
    this.setState({ date: e.target.value })
  }

  render() {
    const time = this.state.time
    const date = this.state.date
    return (
      <User>
        {({ data: { me } }) => {
          const business = me.businessName.toString()
          const phone = me.cellPhone.toString()
          return (
            <Back>
              <Mutation muation={SEND_TEXT_MUTATION} variables={{}}>
                {({ data }) => (
                  <Query query={TEXT_TEMPLATES_QUERY}>
                    {({ data }) => {
                      return (
                        <>
                          <DaySelector handleDate={this.handleDate} />
                          <Timer />
                          <MessageEdit
                            readOnly
                            max={160}
                            defaultValue={data.textTemplates[0].content
                              .replace('<business>', business)
                              .replace('<phone>', phone)
                              .replace('<time>', time)
                              .replace('<date>', format(date, 'MMM Do'))}
                          />
                        </>
                      )
                    }}
                  </Query>
                )}
              </Mutation>
            </Back>
          )
        }}
      </User>
    )
  }
}
export default ReviewMessage
export { TEXT_TEMPLATES_QUERY }
